.Register{
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
    
  background: -webkit-repeating-linear-gradient(
    100deg,
    #39145c,
    #39145c 20%,
    white 20%,
    white 81%
  );
    
  background: repeating-linear-gradient(
    350deg,
    #39145c,
    #39145c 20%,
    white 20%,
    white 81%
  );
}
.RegisterBrainster{
  margin-top: 200px;
}
.RegisterForm{
  width: 50%;
  margin-left: 25%;
  margin-top: 100px;
}
.RegisterForm h3{
  text-transform: uppercase;
  font-weight: bold;
}
.Register .Label{
  color: rgb(66, 66, 66); 
